.header {
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 2;
  background-color: #fff;
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.1);
}
@media (max-width: 1330px) {
  .header {
    height: 90px;
  }
}
@media (max-width: 990px) {
  .header {
    height: 70px;
  }
}
.header__inner {
  display: flex;
  justify-content: space-between;
}
.header__logo {
  margin-right: 20px;
  display: flex;
}
@media (max-width: 990px) {
  .header__logo {
    margin-right: 10px;
  }
}
.header__wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
@media (max-width: 990px) {
  .header__wrapper {
    display: none;
  }
}
.header__location {
  margin-bottom: 8px;
  margin-left: -5px;
}
@media (max-width: 1330px) {
  .header__location {
    margin-bottom: 3px;
  }
}
.header__location a {
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: var(--text-color-dark);
  padding: 5px;
}
@media (max-width: 1330px) {
  .header__location a {
    font-size: 12px;
  }
}
.header__location a.arrow {
  position: relative;
  padding-right: 20px;
}
@media (max-width: 1330px) {
  .header__location a.arrow {
    padding-right: 15px;
  }
}
.header__location a.arrow:after {
  content: "";
  position: absolute;
  background-image: url("../assets/img/icons/small-down-arrow.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 10px;
  height: 10px;
  right: 5px;
  top: 12px;
}
@media (max-width: 1330px) {
  .header__location a.arrow:after {
    background-image: url("../assets/img/icons/small-down-arrow-1330px.svg");
    width: 5px;
    height: 5px;
  }
}
.header .menu {
  margin-right: 20px;
}
.header .menu ul {
  display: flex;
  justify-content: space-between;
}
.header .menu .topmenu > li {
  position: relative;
  margin-right: 30px;
}
.header .menu .topmenu > li > .menu__link {
  font-weight: 700;
  font-size: 16px;
  line-height: 140%;
  letter-spacing: 0.02em;
  white-space: nowrap;
  color: var(--text-color-dark);
}
@media (max-width: 1330px) {
  .header .menu .topmenu > li > .menu__link {
    font-size: 12px;
  }
}
.header .menu .arrow {
  position: relative;
  padding-right: 20px;
}
@media (max-width: 1330px) {
  .header .menu .arrow {
    padding-right: 15px;
  }
}
.header .menu .arrow:after {
  content: "";
  position: absolute;
  background-image: url("../assets/img/icons/small-down-arrow.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 10px;
  height: 10px;
  right: 5px;
  top: 6px;
}
@media (max-width: 1330px) {
  .header .menu .arrow:after {
    background-image: url("../assets/img/icons/small-down-arrow-1330px.svg");
    width: 5px;
    height: 5px;
  }
}
.header .menu .submenu {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  position: absolute;
  left: 0;
  top: 100%;
  z-index: 5;
  width: auto;
  opacity: 0;
  transform: scaleY(0);
  transform-origin: 0 0;
  transition: 0.2s ease-in-out;
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.1);
}
.header .menu .submenu > li {
  border-bottom: 1px solid #E4E5E6;
  height: 50px;
  display: flex;
  align-items: center;
  margin: 0 10px;
}
@media (max-width: 1330px) {
  .header .menu .submenu > li {
    height: 34px;
  }
}
.header .menu .submenu .submenu__link {
  color: var(--text-color-dark);
  font-weight: 700;
  font-size: 16px;
  line-height: 140%;
  letter-spacing: 0.02em;
  padding: 0 10px;
  transition: all 0.2s linear;
}
@media (max-width: 1330px) {
  .header .menu .submenu .submenu__link {
    font-size: 12px;
  }
}
.header .menu .submenu .submenu__link:hover {
  color: var(--secondary-color);
}
.header .menu .topmenu > li:hover .submenu {
  opacity: 1;
  transform: scaleY(1);
}
.header__icons {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: -5px;
}
.header__icon {
  display: flex;
  align-items: center;
  margin-left: 30px;
}
@media (max-width: 1330px) {
  .header__icon {
    margin-left: 10px;
  }
}
@media (max-width: 990px) {
  .header__icon {
    margin-left: 3px;
  }
}
.header__burger {
  position: relative;
  width: 60px;
  height: 36px;
  padding: 5px;
  display: none;
}
@media (max-width: 1330px) {
  .header__burger {
    width: 41px;
    height: 26px;
  }
}
@media (max-width: 990px) {
  .header__burger {
    display: flex;
  }
}
.header__burger span {
  content: "";
  position: absolute;
  width: 50px;
  height: 2px;
  background-color: var(--text-color-dark);
}
@media (max-width: 1330px) {
  .header__burger span {
    width: 31px;
    height: 1px;
  }
}
@media (max-width: 990px) {
  .header__burger span {
    width: 30px;
  }
}
.header__burger span:before {
  content: "";
  position: absolute;
  width: 50px;
  height: 2px;
  background-color: var(--text-color-dark);
  top: 10px;
}
@media (max-width: 1330px) {
  .header__burger span:before {
    width: 31px;
    height: 1px;
    top: 8px;
  }
}
.header__burger span:after {
  content: "";
  position: absolute;
  width: 50px;
  height: 2px;
  background-color: var(--text-color-dark);
  bottom: 10px;
}
@media (max-width: 1330px) {
  .header__burger span:after {
    width: 31px;
    height: 1px;
    bottom: 8px;
  }
}

.header-home {
  background-color: transparent;
  box-shadow: none;
}
.header-home:before {
  content: "";
  width: 100%;
  height: 120px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  background: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0) 100%);
  opacity: 0.5;
}
.header-home .header__location a {
  color: var(--text-color-light);
}
.header-home .header__location a.arrow:after {
  background-image: url("../assets/img/icons/small-down-arrow-home.svg");
}
@media (max-width: 1330px) {
  .header-home .header__location a.arrow:after {
    background-image: url("../assets/img/icons/small-down-arrow-home-1330px.svg");
  }
}
.header-home .menu .topmenu > li > .menu__link {
  color: var(--text-color-light);
}
.header-home .menu .arrow:after {
  background-image: url("../assets/img/icons/small-down-arrow-home.svg");
}
@media (max-width: 1330px) {
  .header-home .menu .arrow:after {
    background-image: url("../assets/img/icons/small-down-arrow-home-1330px.svg");
  }
}
.header-home .menu .submenu {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  position: absolute;
  left: 0;
  top: 100%;
  z-index: 5;
  width: auto;
  opacity: 0;
  transform: scaleY(0);
  transform-origin: 0 0;
  transition: 0.2s ease-in-out;
}
.header-home .menu .submenu > li {
  border-bottom: 1px solid #E4E5E6;
  height: 50px;
  display: flex;
  align-items: center;
  margin: 0 10px;
}
@media (max-width: 1330px) {
  .header-home .menu .submenu > li {
    height: 34px;
  }
}
.header-home .menu .submenu .submenu__link {
  color: var(--text-color-dark);
  font-weight: 700;
  font-size: 16px;
  line-height: 140%;
  letter-spacing: 0.02em;
  padding: 0 10px;
  transition: all 0.2s linear;
}
@media (max-width: 1330px) {
  .header-home .menu .submenu .submenu__link {
    font-size: 12px;
  }
}
.header-home .menu .submenu .submenu__link:hover {
  color: var(--secondary-color);
}
.header-home .menu .topmenu > li:hover .submenu {
  opacity: 1;
  transform: scaleY(1);
}
.header-home .header__burger span {
  background-color: #fff;
}
.header-home .header__burger span:before {
  background-color: #fff;
}
.header-home .header__burger span:after {
  background-color: #fff;
}

.mobile-menu {
  transform: translateX(100%);
  transition: all 0.2s linear;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: var(--primary-color);
  z-index: 1000;
  overflow: hidden;
}
.mobile-menu__head {
  height: 60px;
  position: relative;
  border-bottom: 1px solid #2a2939;
}
.mobile-menu__head .mobile-menu__location {
  display: flex;
  align-items: center;
  height: 100%;
  margin-left: 20px;
}
.mobile-menu__head .mobile-menu__location a {
  font-weight: 400;
  font-size: 12px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: var(--text-color-light);
  padding: 5px;
}
.mobile-menu__head .mobile-menu__location a.arrow {
  position: relative;
  padding-right: 15px;
}
.mobile-menu__head .mobile-menu__location a.arrow:after {
  content: "";
  position: absolute;
  background-image: url("../assets/img/icons/small-down-arrow-1330px.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 5px;
  height: 5px;
  right: 5px;
  top: 12px;
}
.mobile-menu__nav {
  margin-top: 16px;
}
.mobile-menu__nav li {
  text-align: center;
  margin: 8px 0;
}
.mobile-menu__nav li:last-child {
  margin-bottom: 0;
}
.mobile-menu__nav .nav__link {
  font-weight: 700;
  font-size: 11px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: var(--text-color-light);
}
.mobile-menu__nav .nav__link-arrow {
  position: relative;
  padding-right: 15px;
}
.mobile-menu__nav .nav__link-arrow:after {
  content: "";
  position: absolute;
  background-image: url("../assets/img/icons/small-down-arrow-1330px.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 5px;
  height: 5px;
  right: 5px;
  top: 5px;
}
.mobile-menu .mobile-menu__sublist {
  background-color: #253347;
  padding-bottom: 6px;
  display: none;
}
.mobile-menu .mobile-menu__sublist .nav__sublink {
  font-weight: 700;
  font-size: 11px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: var(--text-color-light);
  padding: 6px 0;
}
.mobile-menu .linkHasChild.active .mobile-menu__sublist {
  display: block;
}

.active {
  transform: translateX(0);
}

.mobile-menu__close {
  display: flex;
  position: absolute;
  top: 15px;
  right: 20px;
  z-index: 1001;
}

.no-scroll {
  overflow: hidden;
}

.location__modal {
  position: fixed;
  z-index: 1000;
  top: -100%;
  left: 50%;
  transform: translate(-50%);
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.1);
}
@media (max-width: 990px) {
  .location__modal {
    width: 100%;
    z-index: 1002;
  }
}
.location__modal-list {
  background-color: #fff;
}
@media (max-width: 990px) {
  .location__modal-list {
    margin: 0 20px;
  }
}
.location__modal-item {
  padding: 5px 20px;
  cursor: pointer;
}
.location__modal-item:hover {
  background-color: var(--secondary-color);
  color: #fff;
}

.visible-location {
  top: 20px;
}
@media (max-width: 990px) {
  .visible-location {
    top: 40px;
  }
}

.location__overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  z-index: 1001;
  background: rgba(16, 16, 16, 0.5);
  backdrop-filter: blur(7.5px);
  display: none;
}

.visible-location-overlay {
  display: block;
}